@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,500;0,800;1,100;1,300;1,400;1,500;1,700&display=swap");
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  font-family: "Poppins", sans-serif;
  background: url(./styles/Assets/side-view-doctor-wearing-hazmat-suit.jpg)
    no-repeat;
  background-size: cover;
  background-position: center;
  margin: 0;
  height: 100vh;
  width: 100%;
  /* overflow: ; */
}
.spinner {
  display: flex;
  height: 100vh;
  justify-content: center;
  align-items: center;
  z-index: 9999;
  background-color: rgba(0, 0, 0, 0.425);
}
.sub-btn {
  margin-top: 30px;
  width: 350px;
}
