.form-container {
  /* Optional: To see the form */

  display: flex;
  flex-direction: column; /*  flex direction is for the direction of the elements */
  justify-content: center; /* Center your form horizontally */
  align-items: center; /* Center your form vertically */
  height: 100vh;
  background: url(./Assets/side-view-doctor-wearing-hazmat-suit.jpg) no-repeat;
  background-size: cover;
  background-position: center;
}

.register-form {
  /* background-color: rgb(255, 127, 229);
  border: 1px solid grey; */
  /* padding: 30px; */
  width: 420px;
  background: transparent;
  color: #fff;
  border: 7px solid rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(5px);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  border-radius: 10px;
  padding: 30px 40px;
}
.register-form h1 {
  font-size: 36px;
  text-align: center;
}
.register-form .input-box {
  position: relative;
  width: 100%;
  height: 50px;
  /* background: salmon; */
  margin: 30px 0;
}
.input-box input {
  width: 100%;
  height: 100%;
  background: transparent;

  border: 2px solid rgba(255, 255, 255, 0.2);
  outline: none;
  border-radius: 40px;
  font-size: 16px;
  color: #fff;
  padding: 10px 35px 10px 10px;
}

.register-form button {
  width: 100%;
  height: 45px;
  background: #008cba;
  border: none;
  outline: none;
  border-radius: 40px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  font-size: 16px;
  color: #333;
  font-weight: 700;
  margin-top: 20px;
}
.register-form .register-link {
  font-size: 14.5px;
  text-align: center;
  margin-top: 20px;
}
.register-link .link {
  color: #fff;
  text-decoration: none;
  font-weight: 700;
  padding: 5px;
}
.register-link .link:hover {
  text-decoration: underline;
}

.logo-container {
  position: absolute;
  top: 0;
  left: 0;
}
