.main {
  padding: 10px;
  height: 100vh;
  backdrop-filter: blur(10px);
}

.layout {
  display: flex;
}
.sidebar {
  min-height: 100%;
  width: 300px;
  border-radius: 5px;
  /* background: transparent; */
  /* background-color: #1a504c; */
  box-shadow: 0 0 10px rgb(4, 4, 4);
  margin-right: 20px;
  margin-bottom: 20px;
  color: rgb(246, 244, 244);
}
.content {
  height: 100%;
  width: 100%;
}
.header {
  height: 10vh;
  margin-bottom: 20px;
  box-shadow: 0 0 10px gray;
}
.body {
  height: 90vh;
  margin-bottom: 20px;
  box-shadow: 0 0 2px gray;
  background: transparent;
}
.logo h6 {
  text-align: center;
  font-size: 1.5rem;
  margin: 20px 0px;
}
.menu {
  margin-top: 50px;
}
.menu-items {
  margin-top: 30px;
  margin-left: 2px;
}
.menu-items a {
  color: #f7fafa;
  text-decoration: none;
  font-size: 1.2rem;
}
.menu-items i {
  font-size: 1.2rem;

  margin: 0 15px;
}
.active {
  background-color: #121414;
  padding: 5px;
  margin: 10 0px;
}
.active a,
.active i {
  color: rgb(240, 232, 242);
}

.header-content {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  height: 60px;
  margin: 0 15px;
}
.header-content i {
  margin-right: 20px;
  font-size: 1.2rem;
}
.header-content a {
  text-decoration: none;
  margin: 0 10px;
  font-size: 1.2rem;
  color: blue;
  text-transform: uppercase;
}
